import * as React from 'react';
import { graphql, PageProps } from 'gatsby';
import Card from '@components/Card';
import Utterances from '@components/Utterances';
import { UTTERANCES_THEME } from '@constants/utterances';
import { DeckdeckgoHighlightCodeCarbonTheme } from '@deckdeckgo/highlight-code';
import { defineCustomElements as deckDeckGoElement } from '@deckdeckgo/highlight-code/dist/loader';
deckDeckGoElement();

import 'katex/dist/katex.min.css';

import {
  PostLayout,
  PostFileContent,
  PostMarkdownContent,
} from './post.styles';

interface PostProps {
  file: {
    ext: string;
    fields: {
      content: string;
      stampObject: {
        modified: number;
        created: number;
      };
    };
  };
  markdownRemark?: {
    html: string;
  };
}

function Post({ data, pageContext }: PageProps<PostProps, PageContextType>) {
  const {
    file: {
      ext,
      fields: { content },
    },
    markdownRemark,
  } = data;

  return (
    <PostLayout>
      <Card>
        <PostFileContent>
          <deckgo-highlight-code
            language={ext.replaceAll('.', '')}
            theme={DeckdeckgoHighlightCodeCarbonTheme.DRACULA}
          >
            <code slot="code">{content}</code>
          </deckgo-highlight-code>
        </PostFileContent>
      </Card>

      {markdownRemark && (
        <Card overflowHidden={false}>
          <PostMarkdownContent
            dangerouslySetInnerHTML={{ __html: markdownRemark.html }}
          />
        </Card>
      )}

      {pageContext.utterancesRepo && (
        <Card>
          <Utterances
            repo={pageContext.utterancesRepo}
            theme={UTTERANCES_THEME['github-light']}
          />
        </Card>
      )}
    </PostLayout>
  );
}

export default Post;

export const query = graphql`
  query ($slug: String!, $slugNoExt: String!) {
    file(relativePath: { eq: $slug }) {
      ext
      fields {
        content
        stampObject {
          modified
          created
        }
      }
    }
    markdownRemark(fields: { slug: { eq: $slugNoExt } }) {
      html
    }
  }
`;
